* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

.heading-1 {
  font-family: var(--font-main);
  color: var(--color-white);
  font-size: 80px;
  font-weight: 400;
  line-height: 100px;
  text-align: center;
}

.heading-2 {
  font-family:  var(--font-main);
  color: var(--color-white);
  font-size: 56px;
  font-weight: 400;
  line-height: 64px;
  text-align: center;
}

.heading-3 {
  font-family: var(--font-main);
  color: var(--color-white);
  font-size: 24px;
  line-height: 27.5px;
  font-weight: 400px;
  text-align: center;
}

.heading-4 {
  font-family: var(--font-main);
  color: var(--color-white);
  text-align: center;
  font-weight: 400px;
  font-size: 16px;
  line-height: 18px;
}

.heading-5 {
  font-family: var(--font-secondary);
  color: var(--color-white);
  text-align: center;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 2.7px;
  line-height: 19px;
}

.heading-5 > span {
  opacity: .25;
  margin-right: 1rem;
  font-weight: 700;
}

.subheading-1 {
  font-family: var(--font-main);
  color: var(--color-white);
  text-align: center;
  font-weight: 400;
  font-size: 28px;
  line-height: 32px;
}

.subheading-2 {
  font-family: var(--font-secondary);
  color: var(--color-purple);
  text-align: center;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 2.4px;
}

.nav-text {
  font-family: var(--font-secondary);
  color: var(--color-white);
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
  letter-spacing: 2.4px;
  padding-bottom: 8px;
}

.nav-text > span {
  margin-right: 11px;
  font-weight: 700;
}

.nav-text.active {
  color: var(--color-white);
  border-bottom: solid 3px var(--color-white);
}

.nav-text.active:hover {
  border-bottom: solid 3px var(--color-white);
}

.nav-text:hover {
  border-bottom: solid 3px var(--color-white);
}

.body-text {
  font-family: var(--font-secondary);
  color: var(--color-purple);
  text-align: center;
  font-size: 15px;
  line-height: 25px;
  font-weight: 400;
}

.slider-1 {
  height: 10px;
  width: 10px;
  border-radius: 100%;
  background-color: var(--color-white);
  opacity: .15;
  cursor: pointer;
}

.slider-1.active {
  opacity: 1;
}

.slider-1.active:hover {
  opacity: 1;
}

.slider-1:hover {
  opacity: .5;
}

.slider-2 {
  font-family: var(--font-main);
  text-align: center;
  font-weight: 400px;
  font-size: 16px;
  line-height: 18px;
  height: 40px;
  width: 40px;
  border-radius: 100%;
  background-color: transparent;
  border: solid 1px rgba(255, 255, 255, 0.25);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.slider-2.active {
  background-color: var(--color-white);
  color: var(--color-black);
}

.slider-2:hover {
  border: solid 1px #fff;
}

.tabs {
  font-family: var(--font-secondary);
  color: var(--color-purple);
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 2.3px;
  padding-bottom: 8px;
  cursor: pointer;
}

.tabs.active {
  color: var(--color-white);
  border-bottom: solid 3px var(--color-white);
}

.tabs.active:hover {
  color: var(--color-white);
  border-bottom: solid 3px var(--color-white);
}

.tabs:hover { 
  border-bottom: solid 3px rgba(255, 255, 255, 0.5);
}

@media screen and (min-width: 768px) {
  .heading-1 {
    font-size: 150px;
    line-height: 150px;
  }

  .heading-2 {
    font-size: 80px;
    line-height: 92px;
  }

  .heading-3 {
    font-size: 40px;
    line-height: 46px;
  }

  .heading-4 {
    font-size: 24px;
    line-height: 27.5px;
  }

  .heading-5 {
    font-size: 20px;
    letter-spacing: 3.4px;
    line-height: 24px;
  }

  .nav-text { 
    line-height: 19px;
    letter-spacing: 2.7px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: solid 3px transparent;
  }

  .nav-text > a > span {
    display: none;
  }

  .nav-text > a { 
    text-decoration: none;
    color: var(--color-white);
  }

  .body-text {
    font-size: 16px;
    line-height: 28px;
  }

  .slider-2 {
    height: 60px;
    width: 60px;
    font-size: 24px;
    line-height: 27.5px;
  }

  .tabs {
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
  }

}

@media screen and (min-width: 1080px) {
  .nav-text > a {
    display: flex;
    gap: min(5%, 11px);
  }

  .nav-text > a > span {
    display: block;
    font-weight: 700;
  }
}

@media screen and (min-width: 1440px) {
  .heading-1 {
    line-height: 172px;
  }

  .heading-2 {
    font-size: 100px;
    line-height: 115px;
  }

  .heading-3 {
    font-size: 56px;
    line-height: 64px;
  }

  .heading-4 {
    font-size: 32px;
    line-height: 37px;
  }

  .heading-5 {
    font-size: 28px;
    line-height: 34px;
    letter-spacing: 4.7px;
  }

  .nav-text {
    font-size: 16px;
    line-height: 19.2px;
    letter-spacing: 2.7px;
  }

  .body-text {
    font-size: 18px;
    line-height: 32px;
  }

  .slider-1 {
    height: 15px;
    width: 15px;
  }

  .slider-2 {
    height: 80px;
    width: 80px;
    font-size: 32px;
    line-height: 37px;
  }

}